.card {
  background: #fff;
  border-radius: 2px;
  height:160px;
  margin: 1rem;
  position: relative;
  width: 160px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;

}

.card > .img-container {
  height: 200%;
  overflow: hidden;
  background:#6CADDC;
  padding: 10px
}
.card > .img-container > img {
  width: 100%;
  height: 100%;
  padding: 10px;

}
.card:hover {
  -webkit-transform: scale(1.10, 1.10);
          transform: scale(1.10, 1.10);
  transition: 0.5s ease
}

.wrapper {
  padding: 50px, 100px, 50px, 100px;
  background: #ECEFF1;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  /* padding: 10px; */
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

.title {
  width: 100%;
  text-align: center;
  font-size: 3rem;
  text-decoration: underline;
  color: #2A77AE;
  margin-bottom: 50px;
}


* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  
  margin: 0;
  height: 100%;
}
.wrapper {
  margin-left: 200px;
  margin-right: 200px;
}
